.header {
    display: flex;
    flex-direction: column;
    align-items: center;
}
.inputWrap {
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid black;
    width: 250px;
    height: 20px;
    padding: 4px;
    margin-bottom: 10px;
}
.input {
    outline: none;
    border: none;
    overflow: hidden;
    width: calc(100% - 16px);
    height: 100%;
}
select:nth-child(2) {
    margin-right: 5px;
}