
.booksList {
    list-style: none;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding: 20px;
}
.bookCard {
    width: 200px;
    background-color: rgb(243, 242, 241);
    padding: 10px;
    margin: 10px;
}
.imgWrap {
    width: 100%;
    display: flex;
    justify-content: center;
    margin-bottom: 5px;
}
.category {
    font-size: 12px;
    margin-bottom: 10px;
    margin-top: 20px;
    color: rgb(220, 211, 203);
    text-decoration: underline;
}
.title {
    font-size: 15px;
    margin-bottom: 5px;
}
.authors {
    font-size: 12px;
    color: rgb(220, 211, 203);
}